@import url(https://unpkg.com/ress/dist/ress.min.css);
#app {
  width: 500px;
  text-align: center;
}
html,
body {
  height: 100%;
}
body {
  background-color: white;
}
.tabMenu button {
  background-color: #845EC2;
  padding: 10px;
  border-radius: 30px;
  margin: 8px;
  width: 100px;
  margin: 20px 0 0 20px;
  font-weight: bold;
  color: white;
  cursor: pointer;
}
.tabMenu button:hover {
  background-color: white;
  padding: 9px;
  border-radius: 30px;
  border: 1px solid;
  border-color: #845EC2;
  margin: 8px;
  width: 100px;
  margin: 20px 0 0 20px;
  font-weight: bold;
  color: #845EC2;
}
.add-button button {
  position: fixed;
  right: 50px;
  bottom: 50px;
  background-color: #845EC2;
  padding: 18px;
  border-radius: 50%;
}
.add-button:hover {
  opacity: 0.5;
}
.add-button img {
  width: 30px;
}
.isDone {
  cursor: pointer;
  width: 20px;
}
.delete {
  cursor: pointer;
  width: 20px;
}
.toDoList li {
  background-color: white;
  padding: 30px;
  list-style: none;
  margin: 20px auto;
  border-top: .1px solid lightgray;
  border-bottom: .1px solid lightgray;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.isDoneList li {
  background-color: white;
  padding: 30px;
  list-style: none;
  margin: 20px auto;
  border-top: .1px solid lightgray;
  border-bottom: .1px solid lightgray;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.items p {
  display: wrap;
}
.taskOptions {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 10px;
}
.tabMenu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
}
.items-part {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: white;
  -webkit-box-shadow: 0 10px 6px -6px #777;
  box-shadow: 0 10px 6px -6px #777;
  width: 300px;
  height: 350px;
  padding: 50px;
  border-radius: 20px;
  color: white;
}
.items-part input,
select {
  border: 0;
  padding: 5px;
  font-size: 12px;
  font-family: Arial, sans-serif;
  border: solid 1px #ccc;
  margin: 0 0 10px;
}
.items-part input {
  width: 200px;
}
.items-part select {
  width: 50px;
  color: gray;
}
.items-part label {
  display: block;
  color: black;
}
.buttons button {
  font-size: 8px;
  background-color: #845EC2;
  padding: 8px;
  border-radius: 15px;
  margin: 10px;
  width: 100%;
}
.buttons {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.items {
  display: block;
}
.project-title {
  font-size: 10px;
  background: rgba(0, 201, 183, 0.8);
  color: white;
  padding: 10px;
  border-radius: 30px;
}
.project-deadline {
  color: gray;
  font-size: 10px;
}
.tabMenu .activeTab {
  background-color: white;
  padding: 9px;
  border-radius: 30px;
  border: 1px solid;
  border-color: #845EC2;
  margin: 8px;
  width: 100px;
  margin: 20px 0 0 20px;
  font-weight: bold;
  color: #845EC2;
}

